export class Solution extends RendererThree {
    dirLight: THREE.DirectionalLight

    constructor(id: string = 'main') {
        super(id)

        this.camera.position.set(0, 80, 80);

        const ambientLight = new THREE.AmbientLight(0xcccccc, 0.25);
        this.scene.add(ambientLight);

        this.dirLight = new THREE.DirectionalLight(0xffffff, 0.5);
        this.scene.add(this.dirLight);
        this.dirLight.color.setHSL(0.1, 1, 0.95);
        this.dirLight.position.set(- 1, 1.75, 1);
        this.dirLight.position.multiplyScalar(30);
        this.dirLight.castShadow = true;
        const d = 50;
        this.dirLight.shadow.camera.left = - d;
        this.dirLight.shadow.camera.right = d;
        this.dirLight.shadow.camera.top = d;
        this.dirLight.shadow.camera.bottom = - d;
        this.dirLight.shadow.mapSize.width = 2048 * 4;
        this.dirLight.shadow.mapSize.height = 2048 * 4;
        this.dirLight.shadow.bias = -0.0000001;
        this.dirLight.shadow.camera.near = 0.001;
        this.dirLight.shadow.camera.far = 350;

        const vs = []
        const indices = [];
        const colors = []

        for (let i = 0; i < dataFromRhino['v'].length; ++i) {
            const pos = dataFromRhino['v'][i];
            console.log(pos);
            vs.push(+pos[0], +pos[2], +pos[1])
        }
        for (let i = 0; i < dataFromRhino['vc'].length; ++i) {
            const pos = dataFromRhino['vc'][i];
            colors.push(...pos)
        }
        for (let i = 0; i < dataFromRhino['f'].length; ++i) {
            const pos = dataFromRhino['f'][i];
            indices.push(pos[2], pos[1], pos[0])
        }

        const geometry = new THREE.BufferGeometry();
        geometry.setIndex(indices);
        geometry.setAttribute('position', new THREE.Float32BufferAttribute(vs, 3));
        geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
        geometry.computeVertexNormals();
        geometry.computeBoundingBox();

        const material = new THREE.MeshLambertMaterial();
        const mesh = new THREE.Mesh(geometry, material);
        mesh.castShadow = true;
        mesh.receiveShadow = true;
        this.scene.add(mesh);

        this.start();
    }
    public update(scene: THREE.Scene) {
        var matrix = new THREE.Matrix4();
        matrix.makeRotationY(Math.PI * 0.0085);
        this.dirLight.position.applyMatrix4(matrix);
    }
}
